<template>
    <div>
        <div>
            <el-input
                    size="small"
                    style="width: 300px;"
                    placeholder="添加职位"
                    prefix-icon="el-icon-circle-plus"
                    @keydown.enter.native = "addPosition"
                    v-model="pos.name">
            </el-input>
            <el-button icon="el-icon-circle-plus"
                    size="small" type="primary" style="margin-left: 10px"
                    @click="addPosition"
            >添加</el-button>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    :data="positions"
                    border
                    stripe
                    size="small"
                    style="width: 70%"
                    @selection-change="selectMany">//这是一个回调函数
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="50">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职位名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间">
                </el-table-column>
                <el-table-column >
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEditView(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" plain size="small" v-bind:disabled="dataArr.length==0" @click="handleDeleteBatch()">批量删除</el-button>
        </div>
        <div>
            <el-dialog
                    title="更新名称"
                    :visible.sync="dialogVisible"
                    width="30%">
                <div>
                    <el-tag>职位名称</el-tag>
                    <el-input v-model="updatePos.name" size="small" style="width: 300px;margin-left: 10px"></el-input>
                </div>
                <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="updatePosition()">确 定</el-button>
            </span>
            </el-dialog>
        </div>

    </div>
</template>

<script>
    export default {
        name: "PostMana",
        data(){
            return {
                pos:{
                    name:'',
                },
                positions: [],
                dataArr:[],
                updatePos:{
                    name:''
                },
                dialogVisible:false,
            }
        },
        mounted(){
            this.initPosition();//初始化时挂载方法执行
        },
        methods:{
            selectMany(val){
                this.dataArr=val
                console.log(this.dataArr)
            },
            handleDeleteBatch(){
                this.$confirm('此操作将永久删除职位, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    /*let ids = '?'
                    this.dataArr.forEach(item=>{
                        ids += 'ids='+item.id+'&'
                    })
                    console.log(ids)
                    this.deleteRequest("/system/basic/pos/deleteBatch/"+ids).then(resp=> {
                        if (resp) {
                            this.initPosition()
                        }
                    });
                    *///第一种是在url后面加参数，下面一种是在往后台传json数据
                    let idss=[]
                    this.dataArr.forEach(item=>{
                        idss.push(item.id)
                    })
                    this.deleteRequest("/system/basic/pos/deleteBatch",idss).then(resp=> {
                        if (resp) {
                            this.initPosition()
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            updatePosition(){
                if(this.updatePos.name){
                    this.postRequest("/system/basic/pos/update",this.updatePos).then(resp=>{
                        if(resp){
                            this.initPosition()
                            this.dialogVisible = false

                        }else {
                            this.message.error("更新名称失败")
                        }
                    })
                }
            },
            addPosition(){
                if(this.pos.name){
                    this.postRequest("/system/basic/pos/add",this.pos).then(resp=>{
                        if(resp){
                            this.initPosition()
                            this.pos.name=''
                        }
                    })
                }else {
                    this.$message.error('职位名称不可以为空')
                }
            },
            showEditView(index,data){
                this.dialogVisible=true;
                this.updatePos= data
            },
            handleDelete(index,data){
                this.$confirm('此操作将永久删除【'+data.name +'】职位, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/pos/delete",data).then(resp=> {
                        if (resp) {
                            this.initPosition()
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            initPosition(){
                this.getRequest("/system/basic/pos/").then(resp => {
                    if(resp){
                        this.positions = resp;
                    }
                })
            }
        }
    }
</script>

