<template>
    <div>
        <div>
            <el-input size="small" v-model="jl.name" style="width: 300px;"
                      prefix-icon="el-icon-search"
            placeholder="添加职称"></el-input>
            <el-select v-model="jl.titlelevel" placeholder="职称等级" size="small" style="margin-left: 10px">
                <el-option
                        v-for="item in titlelevels"
                        :key="item"
                        :label="item"
                        :value="item">
                </el-option>
            </el-select>
            <el-button icon="el-icon-plus" size="small" style="margin-left: 10px" type="danger"
                @click="addJobLevel"
            >添加</el-button>
        </div>
        <div>
            <el-table
                    :data="jls"
                    stripe
                    @selection-change="handleSelectionChange"
                    style="width: 70%">
                <el-table-column
                        type="selection"
                        width="55px">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职称名称"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="titlelevel"
                        label="职称级别">
                </el-table-column>
                <el-table-column
                        prop="createdate"
                        label="创建时间"
                        width="160">
                </el-table-column>
                <el-table-column
                        prop="enabled"
                        label="是否启用"
                        >
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        label="操作">
                    <template slot-scope="scope">
                        <el-button size="small" @click="showEiditView(scope.row)">编辑</el-button>
                        <el-button size="small" type="danger"
                        @click="deleteHandler(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" size="small" :disabled="multipleSelection.length==0"
            @click ="deleteMany">批量删除</el-button>
        </div>
            <el-dialog
                    title="修改职称"
                    :visible.sync="dialogVisible"
                    width="30%">
                <div>
                    <table>
                        <tr>
                            <td>
                                <el-tag>职称名</el-tag>
                            </td>
                            <td>
                                <el-input size="small" v-model="updatejl.name"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <el-tag>职称级别</el-tag>
                            </td>
                            <td>
                                <el-input size="small" v-model="updatejl.titlelevel"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <el-tag>是否启用</el-tag>
                            </td>
                            <td>
                                <el-switch
                                        v-model="updatejl.enabled"
                                        active-text="启用"
                                        inactive-text="禁用">
                                </el-switch>
                            </td>
                        </tr>
                    </table>
                </div>
                <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="doUpdata()">确 定</el-button>
              </span>
            </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "JobMana",
        data(){
            return {
                multipleSelection:[],
                dialogVisible:false,
                updatejl:{
                    name:'',
                    titlelevel:'',
                    enabled:false,
                },
                jl:{
                    name:'',
                    titlelevel:'',
                },
                jls:[],
                titlelevels:[
                    '正高级',
                    '副高级',
                    '中级',
                    '初级',
                    '员级',
                ]
            }
        },
        mounted(){
            this.initJls()
        },
        methods:{
            deleteMany(){
                this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids="?"
                    this.multipleSelection.forEach(item=>{
                        ids+='ids='+item.id+'&'
                    })
                    this.deleteRequest("/system/basic/joblevel/"+ids).then(resp=>{
                        if(resp){
                            this.initJls()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            handleSelectionChange(val){
              this.multipleSelection=val
            },
            doUpdata(){
                this.putRequest("/system/basic/joblevel/",this.updatejl).then(resp=>{
                    if(resp){
                        this.initJls()
                        this.dialogVisible=false;
                    }
                })
            },
            showEiditView(data){
                Object.assign(this.updatejl,data)
                this.dialogVisible=true;
            },
            deleteHandler(data){
                this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/joblevel/"+data.id).then(resp=>{
                        if(resp){
                            this.initJls()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            addJobLevel(){
                if(this.jl.name&&this.jl.titlelevel){
                    this.postRequest("/system/basic/joblevel/",this.jl).then(resp=>{
                        if(resp){
                            this.initJls()
                        }
                    })
                }else {
                    this.$message.error("添加字段不可以为空！")
                }

            },
            initJls(){
                this.getRequest("/system/basic/joblevel/").then(resp=>{
                    if(resp){
                        this.jls = resp
                        this.jl={
                            name:'',
                            titlelevel:'',
                        }
                    }
                })
            }
        }
    }
</script>

<style scoped>

</style>