<template>
    <div>
        <div style="margin-top: 10px; display: flex; justify-content: center">
            <el-input v-model="keywords" prefix-icon="el-icon-search" style="width: 400px"></el-input>
            <el-button icon="el-icon-search" type="primary" style="margin-left: 10px" @click="search(keywords)">search</el-button>
        </div>
        <div style="margin-top: 20px;display: flex; flex-wrap: wrap;justify-content: space-around">
            <el-card class="box-card" v-for="(hr,index) in hrs" :key="index">
                <div slot="header" class="clearfix">
                    <span>{{hr.name}}</span>
                    <el-button style="float: right; padding: 3px 0;color: crimson" type="text" icon="el-icon-delete"
                    @click="deleteHr(hr)">删除</el-button>
                </div>
                <div style="width: 100%;display: flex; justify-content: center">
                    <img :src="hr.userface" :alt="hr.name" :title="hr.name" style="width: 72px;height: 72px;border-radius: 72px">
                </div>
                <div style="margin-top: 20px;">
                    <div style="font-size: 12px;color: #5c84a9">
                        <div>用户名：{{hr.name}}</div>
                        <div>手机号码：{{hr.phone}}</div>
                        <div>电话号码：{{hr.telephone}}</div>
                        <div>地址：{{hr.address}}</div>
                        <div>用户状态：<el-switch
                        v-model="hr.enabled"
                        @change="enabledChange(hr)"
                        active-text="启用"
                        inactive-text="禁用">
                        </el-switch></div>
                        <div>用户角色：<el-tag size="mini" style="margin-right: 5px;" type="success" v-for="(role,indexj) in hr.roles" :key="indexj" >
                            {{role.nameZh}}
                        </el-tag>
                            <el-popover
                                    placement="right"
                                    title="角色列表"
                                    width="200"
                                    @show="showPop(hr)"
                                    @hide="hidePop(hr)"
                                    trigger="click">
                                <el-select v-model="selectedRoles" multiple placeholder="请选择">
                                    <el-option
                                            v-for="(r,indexk) in allRoles"
                                            :key="indexk"
                                            :label="r.nameZh"
                                            :value="r.id">
                                    </el-option>
                                </el-select>
                                <el-button slot="reference" icon="el-icon-more"
                                           type="text"></el-button>
                            </el-popover>

                        </div>
                        <div>备注:{{hr.remark}}</div>
                    </div>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SysHr",
        data(){
            return {
                keywords:'',
                hrs:[],
                allRoles:[],
                selectedRoles:[]
            }
        },
        mounted(){
          this.initHrs();
        },
        methods:{
            deleteHr(hr){
                this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/hr/"+hr.id).then(resp=>{
                        if(resp){
                            this.initHrs()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            search(keywords){
                console.log(keywords);
                this.getRequest("/system/hr/?keywords="+keywords).then(resp=>{
                    if(resp){
                        this.hrs=resp;
                    }
                })
            },
            hidePop(hr){
                let roles = hr.roles;
                let flag=false;
                /*if(roles.length!=this.selectedRoles.length){
                    flag =true;
                }else{
                    for()
                }*/
                if(!flag){
                    let url = '/system/hr/role?hrid='+hr.id;
                    this.selectedRoles.forEach(sr=>{
                        url+='&rids='+sr;
                    })
                    this.putRequest(url).then(resp=>{
                        if(resp){
                            this.initHrs();
                        }
                    })
                }

            },
            showPop(hr){
                let roles = hr.roles;
                this.selectedRoles = [];
                roles.forEach(r=>{
                    this.selectedRoles.push(r.id)
                })
                this.initAllRoles()
            },
            enabledChange(hr){
                this.putRequest("/system/hr/",hr).then(resp=>{
                    if(resp){
                        this.initHrs();
                    }
                })
            },
            initHrs(){
                this.getRequest("/system/hr/?keywords="+this.keywords).then(resp=>{
                    if(resp){
                        this.hrs=resp;
                    }
                })
            },
            initAllRoles(){
                this.getRequest("/system/hr/roles").then(resp=>{
                    if(resp){
                        this.allRoles = resp;
                    }
                })
            }
        }
    }
</script>

<style>
.box-card{
    width: 350px;
    margin-bottom: 20px;

}
</style>