<template>
    <div>
        <el-input
                prefix-icon="el-icon-search"
                placeholder="请输入部门名称进行搜索。。"
                v-model="filterText">
        </el-input>

        <el-tree
                style="font-size: 12px"
                :data="deps"
                :props="defaultProps"
                :expand-on-click-node="false"
                :filter-node-method="filterNode"
                ref="tree">
            <span class="custom-tree-node" style="display: flex ;justify-content: space-between;width: 100%;" slot-scope="{ node, data }">
        <span>{{ node.label }}</span>
        <span>
          <el-button style="padding: 2px"
                  type="primary"
                  size="mini"
                  @click="() => showAddDepView(data)">
            添加部门
          </el-button>
          <el-button
                  style="padding: 2px"
                  type="danger"
                  size="mini"
                  @click="() => deleteDep(data)">
            删除部门
          </el-button>
        </span>
      </span>
        </el-tree>
        <el-dialog
                title="添加部门"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <div>
                    <el-tag style="padding: 2px;margin: 2px;">上级部门</el-tag>
                    <span>{{pname}}</span>
                </div>
                <div>
                    <el-tag style="padding: 2px ;margin: 2px 2px 4px 2px;">部门名称</el-tag>
                    <el-input v-model="dep.name" placeholder="请输入部门名称"/>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doAddDep">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DepMana",
        data(){
            return {
                dialogVisible:false,
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                filterText:'',
                deps:[],
                dep:{
                    name:'',
                    parentid:-1,
                },
                pname:'',
            }
        },
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        mounted(){
            this.initDeps()
        },
        methods: {
            initDep(){
              this.dep={
                  name:'',
                  parentid:-1
              }
              this.pname=''
            },
            addDep2Deps(deps,dep){
              for(let i = 0;i < deps.length;i++){
                  let d = deps[i];
                  if(d.id==dep.parentid){
                      d.children = d.children.concat(dep);
                      return;
                  }else {
                      this.addDep2Deps(d.children,dep)
                  }
                }
            },
            doAddDep(){
                this.postRequest("/system/basic/department/",this.dep).then(resp=>{
                    if(resp){
                        this.dialogVisible=false;
                        //this.initDeps();
                        this.initDep();
                        this.addDep2Deps(this.deps,resp.obj);
                    }
                })
            },
            showAddDepView(data){
                this.pname =data.name;
                this.dep.parentid = data.id;
                this.dialogVisible=true;

            },
            removeDepFromDeps(deps,id){
                for(let i =0;i<deps.length;i++){
                    let d=deps[i]
                    if(d.id==id){
                        deps.splice(i,1)
                        return
                    }else {
                        this.removeDepFromDeps(d.children,id);
                    }
                }
            },
            deleteDep(data){
                if(data.parent){
                    this.$message.error("父部门删除失败")
                }else {
                    this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.deleteRequest("/system/basic/department/"+data.id).then(resp=>{
                            if(resp){
                                this.removeDepFromDeps(this.deps,data.id)
                            }
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                }

            },
            filterNode(value, data) {
                if (!value) return true;
                return data.name.indexOf(value) !== -1;
            },
            initDeps(){
                this.getRequest("/system/basic/department/").then(resp=>{
                    if(resp){
                        this.deps=resp;
                    }
                })
            }
        },

    }
</script>

<style scoped>

</style>