<template>
    <div>
        <div class="authTool">
            <el-input placeholder="请输入角色英文名" v-model="role.name">
                <template slot="prepend">ROLE_</template>
            </el-input>
            <el-input placeholder="请输入角色中文名" v-model="role.nameZh"></el-input>
            <el-button type="primary" size="small" @click="doAddRole">添加角色</el-button>
        </div>
        <div style="margin-top: 10px;width: 700px;">
            <el-collapse v-model="activeName" accordion @change="change">
                <el-collapse-item :title="r.nameZh" :name="r.id" v-for="(r,index) in roles" :key="index">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <el-button style="float: right; padding: 3px 0;color: #ff7477;"
                                       icon="el-icon-delete"
                                       type="text"
                                       @click="deleteRole(r)"
                            >删除</el-button>
                        </div>
                        <div>
                            <el-tree
                                    show-checkbox
                                    node-key="id"
                                    ref="tree"
                                    :key="index"
                                    :default-checked-keys="selectedMenus"
                                    :data="allMenus" :props="defaultProps" ></el-tree>
                            <div style="display: flex; justify-content: flex-end">
                                <el-button size="mini">取消修改</el-button>
                                <el-button size="mini" type="primary" @click="doUpdate(r.id,index)">确认修改</el-button>
                            </div>
                        </div>
                    </el-card>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        name: "AuthMana",
        data(){
            return{
                allMenus:[],
                selectedMenus:[],
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                activeName:-1,
                role:{
                    name:'',
                    nameZh:'',
                },
                roles:[]
            }
        },
        mounted(){
            this.initRoles()
        },
        methods:{
            deleteRole(r){
                this.$confirm('此操作将永久删除'+r.nameZh+', 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/auth/role/"+r.id).then(resp=>{
                        if(resp){
                            this.initRoles()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            doAddRole(){
                if(this.role.name&&this.role.nameZh){
                    this.postRequest("/system/basic/auth/role",this.role).then(resp=>{
                        if(resp){
                            this.role.name=''
                            this.role.nameZh=''
                            this.initRoles()
                        }
                    })
                }else {
                    this.$message.error("数据不可以为空")
                }

            },
            doUpdate(rid,index){
                let tree =this.$refs.tree[index];
                let selectedKeys = tree.getCheckedKeys(true);
                let url = '/system/basic/auth/?rid='+rid;
                selectedKeys.forEach(key=>{
                    url+='&mids='+key;
                })
                this.putRequest(url).then(resp=>{
                    if(resp){
                        this.initRoles()
                        this.activeName=-1;
                    }
                })
            },
            change(rid){
                if(rid){
                    this.initAllMenus()
                    this.initSelectedMenus(rid)
                    console.log(rid)
                }
            },
            initSelectedMenus(rid){
                this.getRequest("/system/basic/auth/mids/"+rid).then(resp=>{
                    console.log(resp)
                    this.selectedMenus = resp;
                })
            },
            initAllMenus(){
                this.getRequest("/system/basic/auth/menus").then(resp=>{
                    if(resp){
                        this.allMenus = resp
                    }
                })
            },
            initRoles(){
                this.getRequest("/system/basic/auth/").then(resp=>{
                    if(resp){
                        this.roles = resp
                    }
                })
            }
        }
    }
</script>

<style>
    .authTool {
        display: flex;
    }
    .authTool .el-input{
        width: 300px;
    }
</style>